
.container {
    width: 100%;
    }

.about-container {
    margin: auto;
    width: 90%;
    height: 420px;
   
}
.about-info{
    text-align: center;
}
.about-title{
    margin-top: 1rem;
    font-family: 'Cookie', cursive;
    font-size: 2rem;
}
.about-paragraph{
    font-size: 1rem
}
.about-img img{
    width: 400px;
}
.pictures{
    width: 400px;
    margin: auto;
}


@media screen and (min-width: 678px){
    
    .about-container {  
        width: 80%;
        margin:auto;
        height: 400px; 
        display: flex;    
        justify-content: space-between;
        align-items: center;   
    }
    .about-info{
        width: 60%;
        font-size: 0.8rem;
        text-align: center;
    }
    .about-paragraph{
        font-size: 0.8rem
    }
    .pictures {
        height:320px;
        margin:auto
    }
   
    }
