.nav {
    display: flex;
    position: fixed;
    top: 0;
    height: 80px;
    width: 100%;
    padding: 10px;
    z-index: 1;

    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav-black {
    background-color: turquoise;
}

.logo {
    width: 80px;
    position: fixed;
    left: 20px;
    object-fit: contain;
}

.nav-menu {
    position: fixed;
    right: 40px;
    top:30px;
    display: flex;
    list-style: none;
   font-weight: 600;
}

.nav-links {
    color: black ;
    padding: 0.5rem 1rem;
    text-decoration: none;
    display: flex;
    align-items: center;
}

.fa-bars,
.fa-times {
    color: #fff;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {

    .nav-menu {
        display: flex;
        flex-direction: column;
        justify-items: center;
        width: 100%;
        height: 100vh;
        position: absolute;
        top: 100px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
        margin: auto;
    }

    .nav-menu.active {
        background: turquoise;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }


    .nav-links {
        color: black;
        font-size: 2rem;
        text-align: center;
        padding: 2rem;
        width: 50%;

    }

    .nav-links:hover {
        color: #242424;
        background-color: white;
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
        margin-right: 15px;
    }
}