.banner-container{
    background-image: url(../../images/banner.jpg);
    background-position: center;
    background-size: cover;
    height: 400px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.banner-title{
    font-size: 6rem;
text-align: center;
font-family: 'Cookie', cursive;
}
.banner-subtitle{
    text-align: center;
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
    font-weight: bolder;
}



@media screen and (min-width: 768px){
    .banner-container{
        height: 600px;
    }
    .banner-info{
        margin-left: 30px;
    }
        .banner-title {
            font-size: 10rem;
                }
        .banner-subtitle {
            font-size: 2rem;
        }
    
    }