.gallery-container{
    height: 1100px;
    }
.gallery-img{
    filter: brightness(50%);
    width: 350px;
    height: 350px;
    margin: auto;
    margin-bottom: 15px;
}
.gallery-img:hover{
    filter: brightness(100%);
  }
.gallery-img img{
    max-width: 100%;
    max-height: 100%;
}



@media screen and (min-width: 678px){
    .gallery-container{
        height: 400px;
        }
    .gallery{
        display: flex;
        margin: auto;
        width: 100%;
        }
        .gallery-img{
        
            width: 500px;
            height: 500px;
            padding: 10px;        
        }
}
